import { useContext } from "react";
import styles from "./HeaderYourWalksButton.module.css";
import WalksContext from "../../store/walks-context";

const HeaderYourWalksButton = (props) => {
  const walksCtx = useContext(WalksContext);

  const numberOfWalks = walksCtx.walks.length;

  return (
    <button
      className={styles["nav-button"]}
      type="button"
      onClick={props.onClick}
    >
      <img
        alt="man walking with dog icon"
        src="data:image/svg+xml;base64,"
      />
      <p>
        Your<br></br>Walks
      </p>
      <div className={styles.badge}>{numberOfWalks}</div>
    </button>
  );
};

export default HeaderYourWalksButton;
